/** Media Queries **/
.webp .dark-mode {
  background-image: url(/build/img/bg-dark.webp);
}

.no-webp .dark-mode {
  background-image: url(/build/img/bg-dark.png);
}

.dark-mode h1, .dark-mode h2, .dark-mode h3, .dark-mode h4, .dark-mode p {
  color: #e1e1e1;
}
.dark-mode .navegacion {
  background: linear-gradient(-135deg, #3b055f, #045676);
}
@media (min-width: 1024px) {
  .dark-mode .navegacion {
    background: rgba(0, 0, 0, 0);
  }
}
.dark-mode .header .btn-menu {
  background: linear-gradient(-135deg, #3b055f, #045676);
}
.dark-mode .contenido {
  background-color: rgba(65, 65, 65, 0.3);
  box-shadow: 2px 2px 10px rgba(0, 0, 0, 0.603);
}
.dark-mode .avatar {
  background: #888888;
  box-shadow: 2px 2px 10px rgba(0, 0, 0, 0.603);
}
.dark-mode .icon i, .dark-mode .icon span {
  color: #e1e1e1;
}
.dark-mode .card {
  background-color: rgba(65, 65, 65, 0.1);
  box-shadow: 2px 2px 10px rgba(0, 0, 0, 0.603);
}
.dark-mode .card h3 {
  color: #079DD9;
}
.dark-mode .formulario {
  background-color: rgba(65, 65, 65, 0.1);
  box-shadow: 2px 2px 10px rgba(0, 0, 0, 0.603);
}
.dark-mode input,
.dark-mode textarea {
  background: #5a5a5a;
}
.dark-mode .btn {
  background: linear-gradient(-135deg, #770BBF, #079DD9);
  color: #FFFFFF;
  font-weight: 700;
  text-decoration: none;
  padding: 1rem 3rem;
  text-align: center;
  margin-top: 2.5rem;
  display: block;
  border: none;
  cursor: pointer;
  border-radius: 0.5rem;
  transition: 0.5s;
}
.dark-mode .btn:hover {
  transform: scale(0.9);
}
.dark-mode .info-contacto {
  background-color: #045676;
}
.dark-mode .info-contacto a {
  color: #e1e1e1;
}

html {
  font-size: 62.5%;
  box-sizing: border-box;
}

*, *:before, *:after {
  box-sizing: inherit;
}

body {
  font-family: "Montserrat", sans-serif;
  font-size: 1.6rem;
  line-height: 1.8;
  background-size: 850px;
  background-repeat: no-repeat;
  background-attachment: fixed;
  background-position: center center;
  display: flex;
  flex-direction: column;
  align-items: center;
}
@media (min-width: 768px) {
  body {
    background-size: cover;
  }
}

.webp body {
  background-image: url(/build/img/bg.webp);
}

.no-webp body {
  background-image: url(/build/img/bg.pbg);
}

p {
  color: #000000;
  font-size: 2rem;
}

.contenedor {
  width: 80%;
  display: flex;
  justify-content: center;
  align-items: center;
  padding: 3rem 0 3rem 0;
}

.contenido {
  background-color: rgba(255, 255, 255, 0.4);
  backdrop-filter: blur(20px);
  box-shadow: 2px 2px 10px rgba(51, 51, 51, 0.603);
  width: 100%;
  height: 80%;
  min-height: 700px;
  border-radius: 3rem;
  display: flex;
  justify-content: center;
  align-items: center;
  flex-direction: column;
  text-align: center;
  padding: 4rem;
}

.btn {
  background: linear-gradient(-135deg, #770BBF, #079DD9);
  color: #FFFFFF;
  font-weight: 700;
  text-decoration: none;
  padding: 1rem 3rem;
  text-align: center;
  margin-top: 2.5rem;
  display: block;
  border: none;
  cursor: pointer;
  border-radius: 0.5rem;
  transition: 0.5s;
}
.btn:hover {
  transform: scale(0.9);
}

a {
  text-decoration: none;
}

img,
picture {
  width: 100%;
  display: block;
}

h1, h2, h3, h4 {
  margin: 0;
}

h1 {
  font-size: 3.8rem;
}

h2 {
  font-size: 3.4rem;
  margin: 0 0 7rem 0;
}

h3 {
  font-size: 3rem;
  margin: 2rem 0;
}

h4 {
  font-size: 2.6rem;
}

.main {
  width: 100%;
  height: 100vh;
  display: flex;
  flex-direction: column;
  align-items: stretch;
}

.navegacion {
  width: 100%;
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  position: fixed;
  width: 100%;
  height: 100%;
  background: linear-gradient(-135deg, #770BBF, #079DD9);
  clip-path: circle(25px at calc(100% -45px) 45px);
  transition: all 0.3s ease-in-out;
  z-index: 9;
}
.navegacion a {
  font-weight: 700;
  color: #FFFFFF;
  position: relative;
  display: flex;
  flex-direction: column;
  height: auto;
  justify-content: center;
  align-items: center;
  margin-top: 3rem;
  padding: 0.5rem;
}
.navegacion a:after {
  content: "";
  width: 120%;
  height: 3rem;
  background: rgba(255, 255, 255, 0.5);
  position: absolute;
  border-radius: 50px;
  transform: scaleY(0);
  transition: 0.3s;
  padding: 2rem;
  z-index: -1;
}
.navegacion a:hover:after {
  transform: scaleY(1);
}
.navegacion a:hover {
  color: #340B8C;
}
@media (min-width: 1024px) {
  .navegacion {
    justify-content: flex-end;
    flex-direction: row;
    height: 12rem;
    background: rgba(0, 0, 0, 0);
    clip-path: circle(75%);
    position: relative;
  }
  .navegacion a {
    margin-top: 0;
    margin-right: 5rem;
  }
}

#active:checked ~ .navegacion {
  clip-path: circle(75%);
}

.header input {
  display: none;
}
.header .btn-menu {
  position: fixed;
  right: 2rem;
  top: 2rem;
  z-index: 10;
  text-align: center;
  line-height: 5rem;
  height: 5rem;
  width: 5rem;
  background: linear-gradient(-135deg, #770BBF, #079DD9);
  border-radius: 50%;
  color: #FFFFFF;
  font-size: 2rem;
  cursor: pointer;
  transition: all 0.3s ease;
}
@media (min-width: 1024px) {
  .header .btn-menu {
    display: none;
  }
}

#active:checked ~ .btn-menu {
  background: #FFFFFF;
  color: #340B8C;
}

#active:checked ~ .btn-menu i:before {
  content: "";
}

.btn-dark {
  font-size: 3rem;
}

.contenedor-avatar {
  width: 100%;
  height: 100%;
  display: flex;
  justify-content: center;
  align-items: center;
}

.avatar {
  height: 15rem;
  width: 40rem;
  display: flex;
  justify-content: space-between;
  align-items: center;
  border-radius: 15rem;
  box-shadow: 2px 2px 10px rgba(51, 51, 51, 0.603);
  background: #FFFFFF;
}
.avatar img {
  height: 15rem;
  width: 15rem;
}
.avatar h1 {
  text-transform: uppercase;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  line-height: 3rem;
  margin: 2.5rem 3rem 0 3rem;
  width: 100%;
  text-align: center;
  font-size: 3rem;
}
.avatar span {
  font-size: 1rem;
}
@media (min-width: 768px) {
  .avatar {
    height: 30rem;
    width: 72rem;
    border-radius: 15rem;
  }
  .avatar img {
    height: 30rem;
    width: 30rem;
  }
  .avatar h1 {
    line-height: 5rem;
    margin: 4rem 3rem 0 3rem;
    font-size: 6rem;
  }
  .avatar span {
    font-size: 2rem;
  }
}

#sobre-mi p {
  margin-bottom: 12rem;
}

.tecnologias-grid {
  width: 100%;
}
@media (min-width: 480px) {
  .tecnologias-grid {
    display: grid;
    gap: 1.25rem;
    grid-template-columns: repeat(2, 1fr);
    grid-gap: 2rem;
  }
}
@media (min-width: 768px) {
  .tecnologias-grid {
    display: grid;
    gap: 1.25rem;
    grid-template-columns: repeat(3, 1fr);
    grid-gap: 2rem;
  }
}
@media (min-width: 1024px) {
  .tecnologias-grid {
    display: grid;
    gap: 2.5rem;
    grid-template-columns: repeat(5, 1fr);
    grid-gap: 4rem;
  }
}

.tecnologia {
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
}

.icon {
  width: 5rem;
}
.icon i {
  font-size: 5rem;
}

@media (min-width: 1080px) {
  .proyectos {
    display: grid;
    gap: 1.25rem;
    grid-template-columns: repeat(2, 1fr);
  }
}
@media (min-width: 1650px) {
  .proyectos {
    display: grid;
    gap: 1.25rem;
    grid-template-columns: repeat(3, 1fr);
  }
}

.card {
  background-color: rgba(225, 225, 225, 0.3);
  backdrop-filter: blur(10px);
  box-shadow: 2px 2px 5px rgba(51, 51, 51, 0.5);
  overflow: hidden;
  border-radius: 2rem;
  width: 35rem;
  height: 25rem;
  cursor: pointer;
  transition: 0.4s;
  margin: 3rem;
  display: flex;
  flex-direction: column;
  justify-content: space-between;
}
.card .descripcion-proyecto {
  visibility: hidden;
  opacity: 0;
  transition: 0.2s;
  display: flex;
  flex-direction: column;
  justify-content: space-around;
}
.card:hover {
  height: 70rem;
}
.card:hover .descripcion-proyecto {
  visibility: visible;
  opacity: 1;
}
.card .btn {
  width: 50%;
  margin: 2rem;
}
.card h3 {
  font-size: 2rem;
  color: #770BBF;
  margin: 3rem 0;
  line-height: 2.5rem;
}
.card p {
  padding: 3rem;
  font-size: 1.8rem;
}

.formulario {
  position: relative;
  border-radius: 2rem;
  display: flex;
  height: 90%;
  align-items: center;
  justify-content: center;
  background-color: rgba(225, 225, 225, 0.3);
  backdrop-filter: blur(10px);
  box-shadow: 2px 2px 5px rgba(51, 51, 51, 0.5);
  padding: 15rem 3rem 3rem 3rem;
  margin-top: 10rem;
}
.formulario input, .formulario textarea {
  display: block;
  margin-bottom: 2rem;
  border: none;
  border-radius: 0.5rem;
  padding: 0.5rem;
  width: 100%;
}
.formulario input:last-of-type {
  margin-bottom: 0;
}
@media (min-width: 1500px) {
  .formulario {
    padding: 3rem 3rem 3rem 20rem;
    height: 40rem;
    width: auto;
  }
}

.inputs {
  display: flex;
  flex-direction: column;
  width: 100%;
}

.nombre-apellido {
  display: flex;
  flex-direction: column;
}
.nombre-apellido input {
  display: inline-flex;
}
.nombre-apellido input:first-child {
  margin-right: 2rem;
}
.nombre-apellido input:last-of-type {
  margin-bottom: 2rem;
}
@media (min-width: 768px) {
  .nombre-apellido {
    flex-direction: row;
    justify-content: space-between;
  }
}

.info-contacto {
  position: absolute;
  top: -10rem;
  width: 30rem;
  height: 20rem;
  background-color: #079DD9;
  border-radius: 2rem;
  padding: 2rem;
  box-shadow: 2px 2px 5px rgba(10, 10, 10, 0.5);
}
.info-contacto p {
  font-size: 1.5rem;
  margin: 0;
}
.info-contacto a {
  font-size: 1.5rem;
  color: #000000;
}
@media (min-width: 1500px) {
  .info-contacto {
    left: -15rem;
    top: 10rem;
  }
}

.enlaces-contacto {
  display: flex;
  justify-content: center;
}
.enlaces-contacto a {
  color: #000000;
  font-size: 3rem;
  margin-right: 2rem;
}
.enlaces-contacto a:last-of-type {
  margin-right: 0;
}

.footer {
  width: 100%;
  display: flex;
  justify-content: space-between;
  align-items: center;
}
.footer p {
  margin-left: 5rem;
  color: #FFFFFF;
  font-size: 0.8rem;
  width: 50%;
}
@media (min-width: 768px) {
  .footer p {
    font-size: 1.5rem;
  }
}